  /* border-bottom: #44cfcb; */
  transition: all 0.5s ease-in-out;
}

.ModalStyle input:focus::-webkit-input-placeholder {
  color: transparent;
}

.ModalStyle input:focus:-ms-input-placeholder {
  color: transparent;
}

.ModalStyle input:focus::placeholder {
  color: transparent;
}
.ModalStyle input::-webkit-input-placeholder {
  color: lightgrey;
  -webkit-transition: color 0.3s ease;
  transition: color 0.3s ease;
}
.ModalStyle input:-ms-input-placeholder {
  color: lightgrey;
  -ms-transition: color 0.3s ease;
  transition: color 0.3s ease;
}
.ModalStyle input::placeholder {
  color: lightgrey;
  transition: color 0.3s ease;
